Stylesheet: Sidebars
******************************************************************/

#sidebar {
  .widget {
    margin-bottom: ($spacer * 2);
  }
}

.widget-title {
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid $hr-border-color;
}

.widget {
  ul {
    padding: 0;
    margin: 0;
    li {
      list-style: none;
    }
  }
  select {
    margin-bottom: 10px;
  }

  .textwidget {
    > *:last-child {
      margin-bottom: 0 !important;
    }
  }
}
