.form-control::-webkit-input-placeholder { color: $gray-600; }
.form-control:-moz-placeholder { color: $gray-600; }
.form-control::-moz-placeholder { color: $gray-600; }
.form-control:-ms-input-placeholder { color: $gray-600; }

.form-horizontal .control-label {
	text-align: left;
}

select, textarea, 
input[type="text"], 
input[type="password"], 
input[type="datetime"], 
input[type="datetime-local"], 
input[type="date"], 
input[type="month"], 
input[type="time"], 
input[type="week"], 
input[type="number"], 
input[type="email"], 
input[type="url"], 
input[type="search"], 
input[type="tel"], 
input[type="color"], 
.uneditable-input {
  @extend .form-control;
}

legend {
	font-size: 20px;
}

label:not(.form-check-label) {
  font-size: $font-size-sm;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  font-weight: normal;
  line-height: normal;
  margin-bottom: 0.5rem;
}
